<template>
    <div class="home" v-if="partners != null">
        <b-row>
            <b-col>
                <Carrousel :slides="slideshow"/>
            </b-col>
        </b-row>

        <b-row>
            <b-col v-for="partner of partners">
                <PartnerCard
                        :link="partner.link"
                        :pictureSrc="partner.picture.url"
                        :pictureAlt="partner.picture.alt"
                />
            </b-col>
        </b-row>

        <product-selection title="Nos meilleures ventes"></product-selection>

        <b-row align-v="center" id="notre-partenaire">
            <b-col sm="12">
                <h2>{{ partnerBanner.title }}</h2>
                <prismic-rich-text class="text-center" :field="partnerBanner.description"></prismic-rich-text>
            </b-col>
            <b-col sm="12" md="6">
                <b-img fluid-grow :src="partnerBanner.picture.url" :alt="partnerBanner.picture.alt"></b-img>
            </b-col>
            <b-col sm="12" md="6">
                <prismic-rich-text :field="partnerBanner.text"></prismic-rich-text>
            </b-col>

        </b-row>

        <b-row style="background: dimgray; margin: 0.01em;">
            <b-col sm="12" md="6">
                <b-img center fluid :src="productBanner.picture_1.url" :alt="productBanner.picture_1.alt"></b-img>
            </b-col>
            <b-col sm="12" md="6">
                <b-img center fluid :src="productBanner.picture_2.url" :alt="productBanner.picture_2.alt"></b-img>
            </b-col>
        </b-row>

        <b-row>
            <b-col cols="12">
                <h2>Nos boutiques</h2>
            </b-col>
            <b-col cols="12" md="6" v-for="shop of shops">
                <shop :city="shop.city" :address="shop.address" :phone="shop.phone"
                      :openingHour="shop.openning_hours"></shop>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import PartnerCard from "../components/PartnerCard"
    import Shop from '../components/Shop'
    import Product from "../components/Product";
    import Carrousel from '../components/Carrousel';
    import ProductSelection from "../components/ProductSelection";

    export default {
        name: 'Home',
        components: {
            Product,
            PartnerCard,
            Shop,
            Carrousel,
            ProductSelection
        },
        data() {
            return {
                slideshow: null,
                partners: null,
                partnerBanner: {
                    title: null,
                    description: null,
                    picture: {
                        url: null,
                        alt: null
                    }
                },
                productBanner: null,
                shops: []
            }
        },
        methods: {
            getContent() {
                this.$prismic.client.getSingle('home')
                    .then((document) => {
                        this.slideshow = document.data.slideshow;
                        this.partners = document.data.partner;
                        this.partnerBanner = document.data.body[0].primary;
                        this.productBanner = document.data.body[1].items[0];
                    })
            },
            getShops() {
                this.$prismic.client.query([this.$prismic.Predicates.at('document.type', 'shop')])
                    .then((response) => {
                        for (let res of response.results) {
                            this.shops.push(res.data);
                        }
                    })
            }
        },
        created() {
            this.getContent();
            this.getShops();
        }
    }
</script>
