<template>
    <div>
        <b-row align-v="center" align-h="around">
            <b-col sm="12">
                <h2>{{title}}</h2>
            </b-col>
            <b-col sm="12" md="3" v-for="product of products">
                <product :name="product.name" :price="product.price"
                         :imageSrc="product.pictures[0].picture.url"
                         :imageAlt="product.pictures[0].picture.alt"
                         :uid="product.uid"
                ></product>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import Product from "../components/Product";

    export default {
        name: 'ProductSelection',
        components: {
            Product
        },
        props: {
            title: null
        },
        data() {
            return {
                products: []
            }
        },
        methods: {
            getProducts() {
                this.$prismic.client.query(
                    this.$prismic.Predicates.at('document.type', 'product'),
                    {pageSize: 4, page: 1, orderings: '[my.video_game.title]'}
                ).then((response) => {
                    for (let res of response.results) {
                        this.products.push({...res.data, uid: res.uid});
                    }
                });
            }
        },
        created() {
            this.getProducts();
        }
    }
</script>
