<template>
    <div class="product" v-on:click="goToDetailsProduct(uid)">
        <div class="image-container">
            <b-img
                    fluid
                    class="preview"
                    :src="imageSrc"
                    :alt="imageAlt"></b-img>
        </div>
        <div class="info-container">
            <p class="name">{{name}}</p>
            <p class="price">{{price}} </p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Product',
        props: {
            uid: null,
            name: null,
            price: null,
            imageSrc: null,
            imageAlt: null
        },
        methods: {
            goToDetailsProduct(uid) {
                this.$router.push('/details-produits/' + uid);
            }
        }
    }
</script>
