import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/produits/:type',
    name: 'Products',
    component: () => import('../views/Products.vue')
  },
  {
    path: '/details-produits/:id',
    name: 'DetailsProduct',
    props: true,
    component: () => import('../views/DetailsProduct.vue')
  },
  {
    path: '/contact',
    name: 'Contact',
    component: () => import('../views/Contact.vue')
  },
  {
    path: '/atelier',
    name: 'Workshop',
    component: () => import('../views/Workshop.vue')
  },
  {
    path: '/cgu',
    name: 'Cgu',
    component: () => import('../views/Cgu.vue')
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

export default router
